.contact-us {
  &__info {
    margin: 0 0 24px;
  }
  &__info--last {
    margin-bottom: 32px;
  }
  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__info-link {
    text-decoration: none;
    color: inherit;
    &:hover {
      color: $colorBlue;
    }
  }
  
  &__field {
    margin: 0 0 16px;
    &--last {
      margin-bottom: 32px;
    }
  }
}
