.menu {
  background-color: $colorBlue;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;
  }

  &__top {
    @include top-actions;
    margin-bottom: 48px;
  }

  &__list {
    margin: 0 0 48px;
    list-style: none;
    padding: 0;
        
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }
  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    font-weight: bold;
    font-style: 22px;
    line-height: 22px;
    color: #fff;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }
  &__call {
    display: block;
    width: max-content;
    font-weight: bold;
    font-style: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #fff;
    text-decoration: none;
  
    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid #fff;
    }
  }
}
